import React from 'react'
import { Link } from 'react-router-dom'
import Header from '../Header'
import Footer from '../Footer'
import { Carousel, Container } from 'react-bootstrap'
import firstImage from "../assets/1.jpg"
import secondImage from "../assets/2.jpg"
import thirdImage from "../assets/3.jpg"
export default function HomePage() {
  return (
    <Container fluid = {true} className="container">
    <Header />
    <Carousel>
      <Carousel.Item>
          <img
              className="d-block w-100"
              src={firstImage}
              height ="720"
              width = "1280"
              alt="First slide"
          />
        <Carousel.Caption>
          <Link to = '/services'><h3>Services</h3></Link>
          <p>MVV will provide vascular surgery care for the adult and elderly
             population as well as providing multiple procedures to create a
             vascular care solution. MVV will provide procedures including,
             but not limited to saphenous vein ablation, varicose vein
             excision, peripheral angiography with intervention involving the
             lower extremities and hospital-based procedures including dialysis
             access creation and aneurysm repair. The value of the practice will
              rely on the ability to improve the vascular health of patients
               within the office setting, keeping hospital-based procedures
                to a minimum..</p>
        </Carousel.Caption>
      </Carousel.Item>
      <Carousel.Item>
        <img
          className="d-block w-100"
          src={secondImage}
          height = "720"
          width = "1280"
          alt="Second slide"
          />

        <Carousel.Caption>
          <Link to = '/contact'><h3>Make an Appointment</h3></Link>
          <p>We can find a time that works for you so that you can get the
             care you need. Call our number at <a href="tel:678-580-1149">678-580-1149</a>,
             or fill out our online form, and we will get in contact to make sure
             you get the care you need</p>
        </Carousel.Caption>
      </Carousel.Item>
      <Carousel.Item>
        <img
          className="d-block w-100"
          height = "720"
          width = "1280"
          src={thirdImage}
          alt="Third slide"
          />

        <Carousel.Caption>
          <Link to = '/review'><h3>Read or Leave a Review</h3></Link>
          <p className= "black-text">Don't take our word for it. See what our previous patients had to
             say about our service, or spread the word about your visit to us,
           letting us know what you did or did not enjoy about our service.</p>
        </Carousel.Caption>
      </Carousel.Item>
            </Carousel>
            <Footer />
  </Container>
  )
}
